___CSS_LOADER_EXPORT___.push([module.id, `:root {
  /* Light mode colors */
  --primary-color: #0095e0;
  --primary-hover: #007ab8;
  --secondary-color: #666666;
  --secondary-hover: #4d4d4d;
  --danger-color: #d81a26;
  --danger-hover: #b0151f;
  --disabled-color: #bebec1;
  --white: #ffffff;
  --black: #000000;
  --background-light: #ffffff;
  --text-light: #333333;
}

[data-theme='dark'] {
  /* Dark mode colors */
  --primary-color: #33aaeb;
  --primary-hover: #5ebdee;
  --secondary-color: #999999;
  --secondary-hover: #b3b3b3;
  --danger-color: #ff3b47;
  --danger-hover: #ff6b74;
  --disabled-color: #4d4d4d;
  --background-light: #1a1a1a;
  --text-light: #ffffff;
}

.btn {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.2s ease-in-out;
  position: relative;
  overflow: hidden;
  text-transform: none;
  letter-spacing: 0;
}

/* Sizes */
.btn-small {
  height: 32px;
  padding: 0 16px;
  font-size: 14px;
}

.btn-medium {
  height: 36px;
  padding: 0 24px;
  font-size: 16px;
}

.btn-large {
  height: 48px;
  padding: 0 32px;
  font-size: 18px;
}

/* Colors & Variants */
.btn-filled.btn-primary {
  background-color: var(--primary-color);
  color: var(--white);
}

.btn-filled.btn-primary:hover:not(:disabled) {
  background-color: var(--primary-hover);
}

.btn-filled.btn-secondary {
  background-color: var(--white);
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.btn-filled.btn-secondary:hover:not(:disabled) {
  background-color: rgba(0, 149, 224, 0.04);
}

.btn-filled.btn-danger {
  background-color: var(--danger-color);
  color: var(--white);
}

.btn-filled.btn-danger:hover:not(:disabled) {
  background-color: var(--danger-hover);
}

.btn-outlined {
  background-color: transparent;
  border: 1px solid currentColor;
}

.btn-outlined.btn-primary {
  color: var(--primary-color);
  border-color: var(--primary-color);
}

.btn-outlined.btn-primary:hover:not(:disabled) {
  background-color: rgba(0, 149, 224, 0.04);
}

.btn-outlined.btn-secondary {
  color: var(--secondary-color);
  border-color: var(--secondary-color);
}

.btn-outlined.btn-secondary:hover:not(:disabled) {
  background-color: rgba(102, 102, 102, 0.04);
}

.btn-outlined.btn-danger {
  color: var(--danger-color);
  border-color: var(--danger-color);
}

.btn-outlined.btn-danger:hover:not(:disabled) {
  background-color: rgba(216, 26, 38, 0.04);
}

.btn-text {
  background-color: transparent;
  padding: 0 8px;
}

.btn-text.btn-primary {
  color: var(--primary-color);
}

.btn-text.btn-primary:hover:not(:disabled) {
  background-color: rgba(0, 149, 224, 0.04);
}

.btn-text.btn-secondary {
  color: var(--secondary-color);
}

.btn-text.btn-secondary:hover:not(:disabled) {
  background-color: rgba(102, 102, 102, 0.04);
}

.btn-text.btn-danger {
  color: var(--danger-color);
}

.btn-text.btn-danger:hover:not(:disabled) {
  background-color: rgba(216, 26, 38, 0.04);
}

/* Disabled state */
.btn:disabled,
.btn-disabled {
  background-color: var(--disabled-color) !important;
  color: var(--white) !important;
  border-color: transparent !important;
  cursor: not-allowed;
  opacity: 0.7;
}

/* Icons */
.btn-icon {
  font-size: 18px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-small .btn-icon {
  font-size: 16px;
}

.btn-large .btn-icon {
  font-size: 20px;
}

/* Loading state */
.btn-loading {
  color: transparent !important;
}

.btn-spinner {
  position: absolute;
  width: 18px;
  height: 18px;
  border: 2px solid currentColor;
  border-radius: 50%;
  border-right-color: transparent;
  animation: spin 0.75s linear infinite;
}

.btn-small .btn-spinner {
  width: 16px;
  height: 16px;
  border-width: 1.5px;
}

.btn-large .btn-spinner {
  width: 20px;
  height: 20px;
  border-width: 2.5px;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
